import React from "react";
import "./Timer.css";

const WARNING_THRESHOLD = 10;
const ALERT_THRESHOLD = 5;
const COLOR_CODES = {
  info: {
    color: "green",
  },
  warning: {
    color: "orange",
    threshold: WARNING_THRESHOLD,
  },
  alert: {
    color: "red",
    threshold: ALERT_THRESHOLD,
  },
};
const TIME_LIMIT = 20;
const FULL_DASH_ARRAY = 283;
let timePassed = 0;
let timerInterval = null;
let remainingPathColor = COLOR_CODES.info.color;
export class Timer extends React.Component {
  constructor(props) {
    super(props);
    this.startTimer();
    this.state = {
      timeLeft: TIME_LIMIT,
      isEnd: false,
      isRestart: false,
      isPause: this.props.isPause,
    };
  }
  formatTimeLeft = (time) => {
    // –ù–∞–∏–±–æ–ª—å—à–µ–µ —Ü–µ–ª–æ–µ —á–∏—Å–ª–æ –º–µ–Ω—å—à–µ –∏–ª–∏ —Ä–∞–≤–Ω–æ —Ä–µ–∑—É–ª—å—Ç–∞—Ç—É –¥–µ–ª–µ–Ω–∏—è –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ 60.
    const minutes = Math.floor(time / 60);
    let seconds = time % 60;
    // –ï—Å–ª–∏ –∑–Ω–∞—á–µ–Ω–∏–µ —Å–µ–∫—É–Ω–¥ –º–µ–Ω—å—à–µ 10, —Ç–æ–≥–¥–∞ –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º –µ–≥–æ —Å 0 –≤–ø–µ—Ä–µ–¥–∏
    if (seconds < 10) {
      seconds = `0${seconds}`;
    }
    // –í—ã–≤–æ–¥ –≤ —Ñ–æ—Ä–º–∞—Ç–µ MM:SS
    return `${minutes}:${seconds}`;
  };

  // –î–µ–ª–∏–º –æ—Å—Ç–∞–≤—à–µ–µ—Å—è –≤—Ä–µ–º—è –Ω–∞ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–Ω—ã–π –≤—Ä–µ–º–µ–Ω–Ω–æ–π –ª–∏–º–∏—Ç
  calculateTimeFraction = () => {
    const rawTimeFraction = this.state.timeLeft / TIME_LIMIT;
    return rawTimeFraction - (1 / TIME_LIMIT) * (1 - rawTimeFraction);
  };

  // –û–±–Ω–æ–≤–ª—è–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ —Å–≤–æ–π—Å—Ç–≤–∞ dasharray, –Ω–∞—á–∏–Ω–∞—è —Å 283
  setCircleDasharray = () => {
    const circleDasharray = `${(
      this.calculateTimeFraction() * FULL_DASH_ARRAY
    ).toFixed(0)} 283`;
    document
      .getElementById("base-timer-path-remaining")
      .setAttribute("stroke-dasharray", circleDasharray);
  };

  setRemainingPathColor = (timeLeft) => {
    const { alert, warning, info } = COLOR_CODES;
    if (timeLeft <= alert.threshold) {
      document
        .getElementById("base-timer-path-remaining")
        .classList.remove(warning.color);
      document
        .getElementById("base-timer-path-remaining")
        .classList.add(alert.color);
    } else if (timeLeft <= warning.threshold) {
      document
        .getElementById("base-timer-path-remaining")
        .classList.remove(info.color);
      document
        .getElementById("base-timer-path-remaining")
        .classList.add(warning.color);
    } else if (timeLeft > warning.threshold) {
      document
        .getElementById("base-timer-path-remaining")
        .classList.remove(alert.color);
      document
        .getElementById("base-timer-path-remaining")
        .classList.remove(warning.color);
      document
        .getElementById("base-timer-path-remaining")
        .classList.add(info.color);
    }
  };

  startTimer = () => {
    timerInterval = setInterval(() => {
      if (!this.state.isEnd) {
        // –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤—Ä–µ–º–µ–Ω–∏, –∫–æ—Ç–æ—Ä–æ–µ –ø—Ä–æ—à–ª–æ, —É–≤–µ–ª–∏—á–∏–≤–∞–µ—Ç—Å—è –Ω–∞  1
        if (!this.props.isPause) {
          timePassed = timePassed >= TIME_LIMIT ? 0 : (timePassed += 1);
          this.setState({
            timeLeft: TIME_LIMIT - timePassed,
            isEnd: false,
          });
          if(this.props.assistantSayTime === this.state.timeLeft) {
            this.props.assistantSay();
          }
          // –û–±–Ω–æ–≤–ª—è–µ–º –º–µ—Ç–∫—É –æ—Å—Ç–∞–≤—à–µ–≥–æ—Å—è –≤—Ä–µ–º–µ–Ω–∏
          document.getElementById("base-timer-label").innerHTML =
            this.formatTimeLeft(this.state.timeLeft);
            this.setRemainingPathColor(this.state.timeLeft);
        } 
        if (this.state.timeLeft <= 0) {
          this.setState({
            timeLeft: 0,
            isEnd: true,
          });
          this.props.endGame();
        }
        this.setCircleDasharray();
      }
    }, 1000);
  };

  endGame = () => {
    this.props.endGame();
  };
  shouldComponentUpdate(nextProps, nextState) {
    if (this.props.update !== nextProps.update) {
      this.restartTimer();
      return true;
    }
    return false;
  }
  restartTimer = () => {
    timePassed = 0;
    this.setState({
      timeLeft: TIME_LIMIT,
      isEnd: false,
    });
  };
  render() {
    let pathClass = `base-timer__path-remaining ${remainingPathColor}`;
    console.log("timer updated");
    return (
      <div className="base-timer">
        <svg
          className="base-timer__svg"
          viewBox="0 0 100 100"
          xmlns="http://www.w3.org/2000/svg"
        >
          <g className="base-timer__circle">
            <circle
              className="base-timer__path-elapsed"
              cx="50"
              cy="50"
              r="45"
            />
            <path
              id="base-timer-path-remaining"
              strokeDasharray="283"
              className={pathClass}
              d="
          M 50, 50
          m -45, 0
          a 45,45 0 1,0 90,0
          a 45,45 0 1,0 -90,0
        "
            ></path>
          </g>
        </svg>
        <span id="base-timer-label" className="base-timer__label">
          {this.formatTimeLeft(this.state.timeLeft)}
        </span>
      </div>
    );
  }
}
