//–ø—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –ø—Ä–∞–≤–∏–ª—å–Ω–æ—Å—Ç—å –≤–≤–æ–¥–∏–º–æ–≥–æ —Å–æ–æ–±—â–µ–Ω–∏—è
function isCorrect(current_name, names) {
  current_name = current_name.toLowerCase();
  let temp = names;
  let lastSym;
  //–µ—Å–ª–∏ –≤–≤–µ–¥–µ–Ω–æ –∫–æ—Ä–æ—Ç–∫–æ–µ –∏–º—è
  if (current_name.length <= 1) {
    return 1;
  }
  //–µ—Å–ª–∏ —Å–æ–æ–±—â–µ–Ω–∏–µ —Å–æ—Å—Ç–æ–∏—Ç –∏–∑ –±–æ–ª–µ–µ —á–µ–º 1 —Å–ª–æ–≤–∞
  if (current_name.includes(" ")) {
    return 2;
  }
  //–ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –∫–∏—Ä–∏–ª–ª–∏—Ü—É
  if (!inCyrillic(current_name)) {
    return 3;
  }
  //–µ—Å–ª–∏ —ç—Ç–æ —Å–ª–æ–≤–æ –Ω–µ –ø–µ—Ä–≤–æ–µ
  if (temp.length !== 0) {
    let lastMsg = temp[temp.length - 1].name.toLowerCase();
    //–ø—Ä–æ—Ö–æ–¥ –ø–æ —Å–∏–º–≤–æ–ª–∞–º –ø–æ—Å–ª–µ–¥–Ω–µ–≥–æ —Å—ã–≥—Ä–∞–Ω–Ω–æ–≥–æ –∏–º–µ–Ω–∏ —Å –∫–æ–Ω—Ü–∞ –¥–ª—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –±—É–∫–≤—ã, —Å –∫–æ—Ç–æ—Ä–æ–π –¥–æ–ª–∂–Ω–æ –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è —Ç–µ–∫—É—â–µ–µ –∏–º—è
    lastSym = getStartSymbol(lastMsg);
    if (current_name[0] !== lastSym) {
      return lastSym;
    }
    if(isExist(current_name, temp)) {
      return 4;
    }
  } 
  return 0;
}

function getStartSymbol(msg) {
  let lastSym;
  let isCorrect = true;
  const wrongLastChar = "—å—ä—ã";
  for (let i = msg.length - 1; i > 0; i--) {
    isCorrect = true;
    for (let ws of wrongLastChar) {
      //–ø—Ä–æ–≤–µ—Ä–∫–∞ —Å–∏–º–≤–æ–ª–∞ –Ω–∞ –æ–¥–∏–Ω –∏–∑ —Ç–µ—Ö, –Ω–∞ –∫–æ—Ç–æ—Ä—ã–µ –Ω–µ –º–æ–∂–µ—Ç –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è –∏–º—è
      if (msg[i] === ws) {
        isCorrect = false;
        break;
      }
    }
    if (isCorrect) {
      lastSym = msg[i]; //–±—É–∫–≤–∞, —Å –∫–æ—Ç–æ—Ä–æ–π –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –≤–≤–µ–¥–µ–Ω–æ —Å–ª–æ–≤–æ
      break;
    }
  }
  return lastSym;
}

function inCyrillic(msg) {
  for (let i of msg) {
    if (
      !(
        (i.codePointAt() <= 1103 && i.codePointAt() >= 1072) ||
        i.codePointAt() === 1105
      )
    )
      return false;
  }
  return true;
}

function isExist(msg, messages) {
  for (let i of messages) {
    if (msg.toLowerCase() === i.name.toLowerCase()) return true;
  }
  return false;
}

export default isCorrect;
